
// 
const newDate = new Date()
const year = newDate.getFullYear()
let month = newDate.getMonth() + 1
let day = newDate.getDate()
month = month >= 10 ? month : '0' + month
day = day >= 10 ? day : '0' + day
const startDate = `${year}-${month}-01`
const endDate = `${year}-${month}-${day}`

import { mapActions, mapGetters } from 'vuex'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  components: {
    Flexbox,
    FlexboxItem,
    Datetime,
    Group,
    XButton
  },
  data () {
    return {
      model: {
        fromDate:startDate,
        toDate:endDate
      },
      shiftsStatis: {
        monthShiftNum: 0,
        weekShiftNum: 0
      }
    }
  },
  methods: {
    ...mapActions([
      'QueryShiftsStatis'
    ]),
    submitSearch () {
      const params = this.model
    	this.$store.commit('putStatis', { params })
      this.$router.push('/user/statisrecord')
    }
  },
  created () {
    const params = {
      fromDate: this.fromDate,
      toDate: this.endDate,
      pageid: 1
    }
    this.QueryShiftsStatis(params).then((res) => {
      this.shiftsStatis = res.shiftsStatis
    })
  }
}



